.popular{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 90vh;
}

.popular h1{
    color: #455c93;
    font-size: 50px;
    font-weight: 600;
}

.popular hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background:  #455c93;
}

.popular-item{
    margin-top: 50px;
    display: flex;
    gap: 30px;
}

@media(max-width:1280px){
    .popular{
        height: 70vh;
    }
    .popular h1{
        font-size:40px;
    }
    .popular hr{
        width: 160px;
        height: 6px;
    }

    .popular-item{
        gap: 20px;
        margin-top: 30px;
    }
}

@media(max-width:1024px){
    .popular{
        height: 60vh;
    }
    .popular h1{
        font-size:30px;
    }
    .popular hr{
        width: 120px;
        height: 4px;
    }

    .popular-item{
        gap: 15px;
        margin-top: 20px;
    }
}

@media(max-width:799px){
    .popular{
        height: 40vh;
        gap: 10px;
    }
    .popular h1{
        font-size:20px;
    }
    .popular hr{
        width: 100px;
    }

    .popular-item{
        gap: 20px;
    }
}

@media(max-width:499px){
    .popular{
        height: 90vh;
    }
    .popular h1{
        font-size:30px;
    }
    .popular hr{
        width: 120px;
        height: 4px;
    }

    .popular-item{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
       
    }
}