.new-collections{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
   margin-bottom: 100px;
}

.new-collections h1{
    color: #455c93;
    font-size: 50px;
    font-weight: 600;
}

.new-collections hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background:  #455c93;
}

.collections{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin: 50px;
    gap: 30px;
}

@media(max-width:1280px){
   
    .new-collections h1{
        font-size:40px;
    }
    .new-collections hr{
        width: 160px;
        height: 6px;
    }

    .collections{
        gap: 20px;
        margin-top: 30px;
    }
}

@media(max-width:1024px){
   
    .new-collections h1{
        font-size:30px;
    }
    .new-collections hr{
        width: 120px;
        height: 4px;
    }

    .collections{
        gap: 15px;
        margin-top: 20px;
    }
}

@media(max-width:799px){
    .new-collections{
       
        gap: 6px;
    }
    .new-collections h1{
        font-size:20px;
    }
    .new-collections hr{
        width: 100px;
    }

    .collections{
        gap: 5px;
    }
}

@media(max-width:499px){
 
    .new-collections h1{
        font-size:30px;
    }
    .new-collections hr{
        width: 120px;
        height: 4px;
    }

    .collections{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
       
    }
}