.relatedproducts{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 90vh;
}

.relatedproducts h1{
    color: #395594;
    font-size: 50px;
    font-weight: 600;
}

.relatedproducts hr{
width: 200px;
height: 6px;
border-radius: 10px;
background: #395594;
}

.relatedproducts-item{
margin-top: 50px;
display: flex;
gap: 30px;
}

@media(max-width:1280px){
    .relatedproducts{
        height: 70vh;
    }
    .relatedproducts h1{
        font-size:40px;
    }
    .relatedproducts hr{
        width: 160px;
        height: 6px;
    }

    .relatedproducts-item{
        gap: 20px;
        margin-top: 30px;
    }
}

@media(max-width:1024px){
    .relatedproducts{
        height: 60vh;
        margin-bottom: 80px;
    }
    .relatedproducts h1{
        font-size:30px;
    }
    .relatedproducts hr{
        width: 120px;
        height: 4px;
    }

    .relatedproducts-item{
        gap: 15px;
        margin-top: 20px;
    }
}

@media(max-width:799px){
    .relatedproducts{
        height: 60vh;
        gap: 6px;
        margin-bottom: 80px;
    }
    .relatedproducts h1{
        font-size:20px;
    }
    .relatedproducts hr{
        width: 100px;
    }

    .relatedproducts-item{
        gap: 25px;
    }
}

@media(max-width:499px){
    .relatedproducts{
        height: 90vh;
    }
    .relatedproducts h1{
        font-size:30px;
    }
    .relatedproducts hr{
        width: 120px;
        height: 4px;
    }

    .relatedproducts-item{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
       
    }
}