.footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
}

.footer-logo{
    display: flex;
    align-items: center;
    gap: 20px;
}


.footer-links{
    display: flex;
    list-style: none;
    gap: 50px;
    color : #252525;
    font-size:20px;
}

.footer-links li{
    cursor: pointer;
}

.footer-social-icon{
    display: flex;
    gap: 20px;
}

.footer-icons-container{
    padding: 10px;
    padding-bottom: 6px;
    background-color: #fbfbfb;
    border: 1px solid #ebebeb;
}

.footer-copyright{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    width: 100%;
    margin-bottom: 30px;
    color: #1a1a1a;
    font-size: 20px;
}

.footer-copyright hr{
    width: 80%;
    border: none;
    border-radius: 10px;
    height: 3px;
    background:#c7c7c7;
}


@media(max-width:799px){
    .footer{
        gap: 20px;
    }

    .footer-logo{
         gap: 10px;
         align-items: end;
    }

    .footer-links{
                 font-size: 15px;
                 gap: 10px;
    }

    .footer-social-icon img{
        width: 22px;
    }

    .footer-copyright{
        font-size: 13px;
    }

}